/* clicker-script-400normal - latin */
@font-face {
  font-family: 'Clicker Script';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src:
    local('Clicker Script Regular '),
    local('Clicker Script-Regular'),
    url('./files/clicker-script-latin-400.woff2') format('woff2'), /* Super Modern Browsers */
    url('./files/clicker-script-latin-400.woff') format('woff'); /* Modern Browsers */
}

